import React, { Component } from 'react';
import './App.css';
import Header from './components/header'
import Card from './components/maincard'
import Skills from './components/skills'
import Interests from './components/interests'
import Photos from './components/photos'
import Contact from './components/contactme'
import Works from "./components/works";
import Footer from "./components/footer";



class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            mainTheme: {
                backgroundColor: "#fec107",
                color: "white"
            },
            secondTheme: {
                color: "#fec107"
            },
            isChristmas: false
        };
    }
    checkChristmas = (dataFromChild) => {
        if(dataFromChild === true){
            this.setState({
                mainTheme: {
                    backgroundColor: "#d42426"
                },
                secondTheme: {
                    color: "#d42426"
                },
                isChristmas: true
            });

        }else{
            this.setState({
                mainTheme: {
                    backgroundColor: "#fec107"
                },
                secondTheme: {
                    color: "#fec107"
                },
                isChristmas: false
            });
        }
        this.setState({ isChristmas: dataFromChild });
    }

    render() {
        return (
            <main>
                <Header theme={this.state.mainTheme} callbackFromParent={this.checkChristmas} isChristmas={this.state.isChristmas}/>
                <Card theme={this.state.mainTheme}  isChristmas={this.state.isChristmas}/>
                <Skills theme={this.state.mainTheme} isChristmas={this.state.isChristmas}/>
                <Works theme={this.state.mainTheme} themeSecond={this.state.secondTheme}/>
                <Interests theme={this.state.mainTheme} themeSecond={this.state.secondTheme}/>
                <Photos theme={this.state.mainTheme} />
                <Contact theme={this.state.mainTheme} />
                <Footer theme={this.state.mainTheme} />
            </main>
        );
    }
}

export default App;
