import React, { Component } from 'react';
import fitoniaLogo from '../img/fitonialogo.png'

const projects = [
    {
        name: "Fitonia",
        type: "Web application on AngularJS",
        description: "A small fitness application I made as my graduation project.",
        tech: "React",
        link: "https://vardjaklap.github.io/FitoniaFinal/",
        logo: <img src={fitoniaLogo} alt=""/>
    },
    {
        name: "Modest",
        type: "Landing page",
        description: "A landing, made from scratch from a template.",
        tech: "ddd",
        link: "https://vardjaklap.github.io/Modest/",
        logo: <i className="fas fa-atlas" ></i>
    }
    // {
    //     name: "dd2d",
    //     type: "ddd",
    //     description: "ddd",
    //     tech: "ddd",
    //     link: "sas"
    // },
];
function NumberList(props) {
    const projects = props.projects;
    const listItems = projects.map((project) =>
        <div className=" col-10 col-md-3" key={project.name}>
            <a href={project.link} data-toggle="tooltip" data-placement="bottom" title={project.description}>
                <div className="workCard" data-toggle="tooltip" data-placement="bottom" >
                    <div className="lineWork themeMain" style={props.theme}></div>
                    <div className="" style={props.themeSecond}>
                        {project.logo}
                    </div>
                    <div className="mainInfoWork">
                        <h2>{project.name}</h2>
                        <p><span>{project.type}</span></p>
                    </div>
                </div>
            </a>
        </div>

    );
    return (
        <div className="col-12 worksCards">
            {listItems}
        </div>
    );
}


export default class Works extends Component {


    componentDidMount() {

    }

    componentDidUpdate() {

    }
    render() {
        return (
            <section id="works">
                <div className="container">
                    <div className="row justify-content-center">
                        <h1 className="sectionHeader">My works</h1>
                    </div>
                    <div className="row justify-content-center">

                                <NumberList projects={projects}  theme={this.props.theme} themeSecond={this.props.themeSecond}/>

                    </div>
                </div>
            </section>
        );
    }
}